'use client'

import { Search, ShoppingCart } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState } from "react";
import { useSelector } from "react-redux";
import {
  SignedIn,
  SignedOut,
  SignInButton,
  UserButton,
} from "@clerk/nextjs";

const Navbar = () => {
  const router = useRouter();

  const [search, setSearch] = useState("");
  const cartCount = useSelector((state) => state.cart.total);

  const handleSearch = (e) => {
    e.preventDefault();
    router.push(`/shop?search=${search}`);
  };

  return (
    <nav className="relative bg-white z-50">
      <div className="mx-6">
        <div className="flex items-center justify-between max-w-7xl mx-auto py-4">

          {/* Logo */}
          <Link href="/" className="relative text-4xl font-semibold text-slate-700">
            <span className="text-green-600">go</span>cart
            <span className="text-green-600 text-5xl leading-0">.</span>
            <p className="absolute text-xs font-semibold -top-1 -right-8 px-3 py-0.5 rounded-full text-white bg-green-500">
              plus
            </p>
          </Link>

          {/* Desktop Menu */}
          <div className="hidden sm:flex items-center gap-4 lg:gap-8 text-slate-600">

            <Link href="/">Home</Link>
            <Link href="/shop">Shop</Link>
            <Link href="/">About</Link>
            <Link href="/">Contact</Link>

            {/* Search */}
            <form
              onSubmit={handleSearch}
              className="hidden xl:flex items-center gap-2 bg-slate-100 px-4 py-3 rounded-full"
            >
              <Search size={18} />
              <input
                className="bg-transparent outline-none placeholder-slate-600"
                type="text"
                placeholder="Search products"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                required
              />
            </form>

            {/* Cart */}
            <Link href="/cart" className="relative flex items-center gap-2">
              <ShoppingCart size={18} />
              Cart
              <span className="absolute -top-1 left-3 text-[10px] text-white bg-slate-600 size-4 rounded-full flex items-center justify-center">
                {cartCount}
              </span>
            </Link>

            {/* Auth */}
            <SignedOut>
              <SignInButton mode="modal">
                <button className="px-8 py-2 bg-indigo-500 hover:bg-indigo-600 transition text-white rounded-full">
                  Login
                </button>
              </SignInButton>
            </SignedOut>

            <SignedIn>
              <UserButton afterSignOutUrl="/" />
            </SignedIn>

          </div>

          {/* Mobile */}
          <div className="sm:hidden">
            <SignedOut>
              <SignInButton mode="modal">
                <button className="px-6 py-1.5 bg-indigo-500 hover:bg-indigo-600 text-sm text-white rounded-full">
                  Login
                </button>
              </SignInButton>
            </SignedOut>

            <SignedIn>
              <UserButton afterSignOutUrl="/" />
            </SignedIn>
          </div>

        </div>
      </div>
      <hr className="border-gray-300" />
    </nav>
  );
};

export default Navbar;
